.portfolio__technologies {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    min-height: 75vh;
}

.portfolio__technologies-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.portfolio__technologies-heading h1 {
    font-size: 62px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    height: 4rem;
}

.portfolio__technologies-heading p {
    color: var(--color-text);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);
    margin-top: 2rem;
}

.portfolio__technologies-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .portfolio__technologies {
        flex-direction: column;
    }

    .portfolio__technologies-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .portfolio__technologies-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}