.portfolio__header{
    display: flex;
    min-height: 100vh;
}

.portfolio__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.portfolio__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.portfolio__header-content p{
    font-family: var(--font-family);
    font-style: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}


.portfolio__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.portfolio__header-image img{
    width: 75%;
    height: auto;
    border-radius: 550px;
}

.portfolio__header-content p{
    font-family: var(--font-family);
    font-style: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}




@media screen and(max-width: 1050px){
    .portfolio__header{
        flex-direction: column;
    }
    .portfolio__header-content{
        margin: 0 0 3rem;
    }
    .typewrite{
        display: none;
    }

}

@media screen and(max-width: 650px){
    .portfolio__header-content h1{
        font-size: 30px;
        line-height: 60px;
        display: none;
    }

    
    .portfolio__header p{
        font-size: 16px;
        line-height: 25px;
    }

}

@media screen and(max-width: 490px){
    .portfolio__header-content h1{
        font-size: 30px;
        line-height: 48px;
    }
    
    .portfolio__header p{
        font-size: 14px;
        line-height: 24px;
    }

    .typewrite{
        display: none;
    }
}

