.portfolio__navbar {
    position: fixed;
    width: 100%;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.portfolio__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.portfolio__navbar-links_logo {
    margin-right: 2rem;
}

.portfolio__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.portfolio__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.portfolio__navbar-icons{
    display: flex;
    color: white;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
}

.icon_portfolio{
    margin-inline: 0.5rem;
}

.portfolio__navbar-links_container p,
.portfolio__navbar-sign p,
.portfolio__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.portfolio__navbar-links_container>p:hover,
.portfolio__navbar-menu_container-links>p:hover{
    color: #05F140;
    transition: all 0.3s ease;
}

.portfolio__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}

.portfolio__navbar-menu svg{
    cursor: pointer;
}

.portfolio__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0,0.2);
}

.portfolio__navbar-menu_container p{
    margin: 1rem 0;

}


@media screen and (max-width: 1050px){
    .portfolio__navbar-links_container{
        display: none;
    }
    .portfolio__navbar-menu{
        display: flex;
    }
    .portfolio__navbar-icons{
        display: none;
    }
    .portfolio__navbar-menu_container-links-icons{
        color: white;
    }
}

@media screen and (max-width: 700px){
    .portfolio__navbar{
        padding: 2rem 4rem;
    }
    .portfolio__header-image{
        display: none;
    }
    .portfolio__navbar-icons{
        display: none;
    }

    
}

@media screen and (max-width: 550px){
    .portfolio__navbar{
        padding: 2rem;
    }
    .portfolio__navbar-icons{
        display: none;
    }
    .portfolio__navbar-menu_container{
        top: 20px;
    }
    .portfolio__navbar-menu_container-links-icons{
        color: white;
    }
}

