.portfolio__footer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    background: var(--color-footer);
}

.portfolio__footer-container-contacts{
    display: flex;
    justify-content: space-between;
}

.portfolio__footer-contact{
    margin-inline-end: 0.5rem;
}

.portfolio__footer-contact>h3{
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 16px;
    color: #FFFFFF;
}

.portfolio__footer-contact>p{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 14px;
    color: #FFFFFF;
    margin-top: 0.5rem;
}

.portfolio__footer-icons{
    color: white;
    cursor: pointer;
    display: flex;
}

@media screen and (max-width:650px){
    .portfolio__footer-icons{
        margin-top: 1.5rem;
        margin-left: -0.5rem;
    }
}